.landscape {
    width: 100%;
    margin: 0 auto;
    position: relative;
    box-sizing: border-box;

    .sVisual {
        width: 100%;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        font-size: 0;
        line-height: 0;
        overflow: hidden;
        position: relative;
        background-image: url('/img/basic-bg.png');

        .inConts {
            width: px-to-rem(1200);
            margin: 0 auto;
            position: relative;
            height: px-to-rem(290);
            padding-bottom: px-to-rem(56);
            display: table;
            box-sizing: border-box;

            h2 {
                text-align: center;
                display: table-cell;
                vertical-align: middle;
            }

            h2>* {
                transition: all 1s ease-out;
                transform-origin: 50% 50%;
                opacity: 1;
                font-weight: 500;
                font-size: px-to-rem(40);
                line-height: 1;
                color: #fff;
                display: inline-block;
                vertical-align: top;
                position: relative;
            }
        }
    }

    .contents {
        width: 100%;
        padding-bottom: 0px;
        overflow: hidden;
        opacity: 1;
        top: 0px;
        padding: 0;
        transition-delay: .2s;
        transition: all .7s ease-out;
        transform-origin: 50% 50%;
        min-height: px-to-rem(460);
        margin: 0 auto;
        position: relative;
        box-sizing: border-box;

        .fullBox {
            text-align: center !important;
            position: relative;

            img {
                width: 100%;
                height: auto;
            }

            .basicInfoArea1 {
                position: absolute;
                top: 20%;
                right: 20%;

                .title {
                    color: rgb(65, 65, 67);
                    font-size: px-to-rem(40);
                    font-weight: 600;
                    margin-bottom: px-to-rem(30);
                }

                .info {
                    color: rgb(103, 101, 102);
                    font-size: px-to-rem(20);
                    font-weight: bold;
                    text-align: right;

                    div {
                        margin-bottom: px-to-rem(10);
                    }
                }
            }

            .basicInfoArea2 {
                position: absolute;
                top: 45%;
                left: 20%;

                .title {
                    color: rgb(0, 0, 0);
                    font-size: px-to-rem(40);
                    font-weight: 600;
                    margin-bottom: px-to-rem(30);
                }

                .info {
                    color: rgb(60, 60, 60);
                    font-size: px-to-rem(20);
                    font-weight: bold;
                    text-align: center;

                    div {
                        margin-bottom: px-to-rem(10);
                    }
                }
            }
        }
    }
}